import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    children: [
      {
        path: "/CANVAS",
        name: "CANVAS",
        component: () =>
          import(/* webpackChunkName: "about" */ "../page/canvas/index.vue"),
        children: []
      },
      {
        path: "/WEBGL",
        name: "WEIGL",
        component: () =>
          import(/* webpackChunkName: "about" */ "../page/webgl/index.vue"),
        children: [
          {
            path: "/WebglInit",
            name: "WEBglIndex",
            component: () =>
              import(
                /* webpackChunkName: "about" */ "../page/webgl/webglInit.vue"
              )
          }
        ]
      },
      {
        path: "/THREE",
        name: "THREE",
        component: () =>
          import(/* webpackChunkName: "about" */ "../page/three/index.vue"),
        children: [
          {
            path: "/ThreeInit",
            name: "threeIndex",
            component: () =>
              import(
                /* webpackChunkName: "about" */ "../page/three/threeInit.vue"
              )
          }
        ]
      }
    ]
  },
  {
    path: "/view",
    name: "View",
    component: () => import(/* webpackChunkName: "about" */ "../views/View.vue")
  },
  {
    path: "/SM",
    name: "SM",
    component: () =>
      import(/* webpackChunkName: "about" */ "../page/sm/comp/NumberComp.vue")
  },
  {
    path: "/CANVASPANTER",
    name: "CANVASPANTER",
    component: () =>
      import(/* webpackChunkName: "about" */ "../page/canvas/canvasPainter.vue")
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});
router.beforeEach((to, from, next) => {
  console.log(to, "前置第一个参数");
  console.log(from, "前置第二个参数");
  console.log(next, "前置第三个参数");
  /*
  to 目标路由
  from 源路由
  next 跳转到下一个路由
  */
  next();
});
export default router;
